.footer {
    padding: 8rem;
    background: #333333;
    &__logo {
        width: 12rem; }
    &__list {
        list-style: none;
        text-align: center; }
    &__item {
        display: inline-block;
        &:not(:last-child) {
            margin-right: 5rem; } }
    &__link {
        &:link, &:visited {
            color: $color-grey;
            text-decoration: none;
            text-transform: uppercase;
            font-size: $font-size-pharagraph;
            letter-spacing: .2rem;
            font-weight: 300;
            display: inline-block;
            transition: all .5s; }
        &:active {}
        &:hover {
            transform: rotate(5deg) scale(1.2);
            color:  $color-brown-dark;
            text-shadow: $shadow-h2; } } }
